@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
  padding:0;
  font-family:Lato, sans-serif;
}
ul {
  text-align:right;
  margin-right:20px;
}
li {
  list-style-type: none;
  display:inline;
}
li:hover {
  color:orange;
}
nav{
  background-color:white;
  width:100%;
  position:-webkit-sticky;
  position:sticky;
  top:0vh;
  padding-top:10px;
  padding-bottom:10px;
}
.navigation {
  margin: 20px;
}
#MainImage {
  width: 100%;
  height: 100vh;
}
#MainWelcome {
  width:100%;
  position: absolute;
  top:50vh;
  color:white;
  font-size:3em;
  text-align:center;
}
#myImage {
  border-radius:100000px;
  width:300px;
  height:300px;
}
#mainContent {
  width:80%;
  margin:0 auto;
  margin-top:3%;
}
#introduction {
  display:grid;
  grid-template-columns: 300px auto;
  grid-column-gap: 3%;
}
#introduction, h2 {

}
#projectsSection {
  display:inline-grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-column-gap: 2%;
}

